.container {
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto; }

#content {
  padding-top: 48px; }

.work_list {
  padding-bottom: 79px;
  &__item {
    height: 246px;
    margin-bottom: 23px;
    position: relative;
    overflow: hidden;
    border-radius: 6px;
    margin-right: 20px;
    &:hover {
      box-shadow: 0px 25px 35px 0px rgba(68, 115, 185, 0.25); }
    a {
      position: relative;
      display: block;
      height: 100%;
      width: 100%;
      img {
        position: absolute;
        left: 0;
        right: 0; }
      &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0.7;
        background-color: rgb(0, 0, 0); }
      &:hover:after {
        background-color: #4473b9;
        opacity: 0.9; } }
    &--title {
      position: absolute;
      left: 48px;
      top: 44px;
      @include FFGB;
      font-size: 18px;
      color: #ffffff;
      line-height: 18px; }
    &--price {
      position: absolute;
      right: 35px;
      bottom: 35px;
      @include FFGB;
      font-size: 12px;
      line-height: 30px;
      height: 30px;
      color: #ffffff;
      vertical-align: middle;
      span {
        font-size: 30px;
        display: inline-block;
        padding-left: 5px;
        padding-right: 5px; } }
    &.item1 {
      max-width: 573px;
      width: 573px; }
    &.item2 {
      max-width: 277px;
      width: 277px;
      .work_list__item--title {
        left: 39px; } }
    &.item3 {
      max-width: 277px;
      width: 277px;
      margin-right: 0;
      .work_list__item--title {
        left: 34px; } }
    &.item4 {
      max-width: 277px;
      width: 277px;
      height: 255px;
      .work_list__item--title {
        left: 36px;
        top: 45px; } }
    &.item5 {
      max-width: 573px;
      width: 573px;
      height: 534px;
      .work_list__item--title {
        left: 47px;
        top: 45px;
        line-height: 24px; } }
    &.item6 {
      max-width: 277px;
      width: 277px;
      height: 534px;
      margin-right: 0;
      a:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #4473b9;
        opacity: 0.9; } }
    &.item7 {
      max-width: 277px;
      width: 277px;
      height: 255px;
      margin-top: -278px;
      .work_list__item--title {
        left: 38px;
        top: 43px;
        line-height: 24px; } } } }

.form-work {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  padding: 35px 28px 0;
  &__title {
    text-align: center;
    font-size: 18px;
    @include FFGB;
    line-height: 23px;
    color: #ffffff;
    padding-bottom: 7px; }
  &__desc {
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    color: #ffffff;
    padding-bottom: 22px; }
  &__row {
    padding-bottom: 10px;
    input, select {
      height: 60px;
      padding-left: 24px;
      padding-right: 24px;
      border: none;
      border-radius: 30px;
      width: 100%; }
    input[type=submit] {
      height: 60px;
      padding-left: 24px;
      padding-right: 24px;
      border: none;
      border-radius: 30px;
      width: 100%;
      font-size: 14px;
      color: #ffffff;
      @include FFGB;
      background-color: #c43939; }
    textarea {
      height: 115px;
      padding: 20px 24px;
      width: 100%;
      border: none;
      border-radius: 30px; } }
  &__link {
    background: none;
    text-align: center;
    a {
      opacity: 1;
      background: none;
      font-size: 12px;
      line-height: 18px;
      color: #ffffff;
      &:after {
        display: none; } } } }

.ourclient {
  background: url(../images/team1-2.png) no-repeat center #fcfcfc;
  padding-top: 79px;
  h2 {
    text-align: center;
    @include FFGB;
    font-size: 40px;
    line-height: 50px;
    padding-bottom: 50px;
    &:before {
      content: '';
      display: block;
      margin: 0 auto;
      width: 41px;
      height: 3px;
      background: #4473b9;
      margin-bottom: 25px; } } }

.client-canal {
  padding-bottom: 120px;
  &:after {
    content: '';
    display: table;
    clear: both; }
  &__item {
    display: block;
    width: 116px;
    height: 53px;
    margin-bottom: 15px;
    img {
      width: 116px;
      height: 53px;
      border: #e4e4e4 solid 1px;
      border-radius: 5px; } }
  &__col-l {
    float: left; }
  &__col-r {
    float: right; }
  &__col-l-2 {
    margin-left: 72px;
    margin-top: 70px;
    float: left; }
  &__col-r-2 {
    margin-top: 70px;
    margin-right: 72px;
    float: right; } }
